import { Button } from '@juuyan.org/ui';
import { useSpring, config } from 'react-spring';

export const Header = () => {
  const [scroll, setScroll] = useSpring(() => ({
    config: config.stiff,
    y: window.scrollY,
    onChange: (y) => {
      console.log(y);
      window.scroll(0, y);
    },
  }));

  return (
    <div className="fixed z-shell inset-x-0 top-0 p-micro-40 flex justify-between">
      <Button
        icon="home"
        onClick={() => {
          setScroll({ y: 0 });
        }}
      />
      <Button icon="mail" href="mailto:hello@juuyan.com?subject=Greeting" />
    </div>
  );
};
